.card {
    flex-grow: 1;
    padding: 32px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.wrapper {
    display: flex;
}

.image {
    position: relative;
    margin: auto;
    display: flex;
    box-shadow: 0 0 50px rgba(96, 4, 50, 0.23);
    font-size: 3px;
}

.img {
    width: 100%;
    max-width: 840px;
    object-fit: cover;
}

